@charset "utf-8";

/* Scss Document */


/*------------------------------
	ナビゲーション
------------------------------*/

/*------------------------------
	パンくずナビ
------------------------------*/
.wp-breadcrumb {
  font-size: 13px;
  margin: 15px auto;
  li {
    float: left;
    margin-right: 1em;
    &:before {
      content: '>';
      padding-right: 1em;
    }
    &:first-child {
      background:none;
      text-indent: 0em;
      &:before {
        content: none;
        padding: 0;
      }
    }
  }
  &.isHome {
    li:last-child {
      display: none;
    }
  }
}

/*------------------------------
	ページネーション
------------------------------*/
.wp-pagenavi {
  text-align: center;
  clear: both;
  margin: 60px 0;
  a, span {
    color: #eb5c02;
    background-color: #FFF;
    border: solid 1px #eb5c02;
    padding: 8px 15px;
    margin: 0 2px;
    white-space: nowrap;
    -moz-border-radius: 3px;
    -webkit-border-radius: 3px;
    border-radius: 3px;
    -webkit-transition: 0.2s ease-in-out;
    -moz-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    text-align: center;
    text-decoration: none;
  }
  a:hover {
    color: #FFF;
    background-color: #f49a62;
    border-color: #fff;
  }
  span.current {
    color: #FFF;
    background-color: #f49a62;
    border-color: #eb5c02;
    font-weight: bold;
  }
}


/*------------------------------
	Search
------------------------------*/
.screen-reader-text{
    display: none;
}
#s{
    border: 1px solid #ddd;
    font-size: 16px;
    line-height: 1.8;
    width: 160px;
}
#searchsubmit{
    background: #999;
    border: 1px solid #ddd;
    color:#fff;
    cursor: pointer;
    padding: 5px 10px;
}


/*------------------------------
	ネイティブCSS
------------------------------*/

/*------------------------------
	Content( 記事の本文 )
------------------------------*/
.wp-content {
	line-height: 1.8;
	margin-bottom: 60px;
	&:after {
		content: "";
		clear: both;
		display: block;
	}
    strong { font-weight: bold; }
    em { font-style: italic; }
    blockquote {
        display: block;
        -webkit-margin-before: 1em;
        -webkit-margin-after: 1em;
        -webkit-margin-start: 40px;
        -webkit-margin-end: 40px;
    }

    /* img */
    .aligncenter {
        display: block;
        margin: 0 auto;
    }
    .alignright {
    	float: right;
    	margin-left: 15px !important;
    }
    .alignleft {
    	float: left;
    	margin-right: 15px !important;
    }

    img[class*="wp-image-"],
    img[class*="attachment-"] {
        height: auto;
        max-width: 100%;
        margin: 15px 0;
    }

    .wp-caption{
    	max-width: 100%;
    }
	
	p {
    	display: block;
		-webkit-margin-before: 1em;
		-webkit-margin-after: 1em;
		-webkit-margin-start: 0px;
		-webkit-margin-end: 0px;	
	}

    ul {
        margin: 1em 0;
        padding-left: 40px;
        list-style-type: disc;
    }

    ol {
        margin: 1em 0;
        padding-left: 40px;
        list-style-type: decimal;
    }

    blockquote{
        margin: 0 0 1em 0;
        padding: 50px 50px 40px;
        background: #f2f2f2;
        background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(248,248,248,1) 100%);
        background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(255,255,255,1)), color-stop(100%,rgba(248,248,248,1)));
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f8f8f8',GradientType=0 );
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
        position: relative;
        border: 1px solid #c1c1c1;
    }

    blockquote:before{
        content: '"';
        font-style: italic;
        font-size: 40px;
        font-weight: bold;
        line-height: 40px;
        width: 30px;
        height: 30px;
        position: absolute;
        top: 20px;
        left: 20px;
        color: #999;
    }

    blockquote:after {
        content: '"';
        font-style: italic;
        font-size: 40px;
        font-weight: bold;
        text-align: left;
        line-height: 60px;
        width: 30px;
        height: 30px;
        position: absolute;
        bottom: 20px;
        right: 20px;
        color: #999;
    }

    pre  {
        margin: 1em 0;
        padding: 1em;
        color: #000000;
        white-space: pre-wrap;       /* css-3 */
        white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
        white-space: -pre-wrap;      /* Opera 4-6 */
        white-space: -o-pre-wrap;    /* Opera 7 */
        word-wrap: break-word;       /* Internet Explorer 5.5+ */
    }

	// 見出しのスタイル
    h2 {
        border-bottom: solid 1px #333;
        color: #333;
        font-size: 24px;
        font-weight: 700;
        padding: 5px 0;
        margin: 30px 0;
    }
    h3 {
        font-size: 21px;
        line-height: 120%;
        margin: 30px 0;
        font-weight: 700;
        line-height: 120%;
        padding: 10px 14px;
        background: #f3f3f3;
        border-bottom: solid 1px #333;
    }
    h4 {
        border-left: solid 4px #333;
        border-bottom: solid 1px #333;
        font-size: 18px;
        padding: 5px 10px;
        margin: 30px 0;
    }

}
@include max-screen ($bp-sp) {
  .wp-content {
    h2 {
        font-size: 18px;
    }
    h3 {
        font-size: 16px;
    }
    h4 {
        font-size: 14px;
    }
  }
}